@import url("https://fonts.googleapis.com/css2?family=Inter&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Inter;
}
body {
  width: 100%;
  background-color: hsl(36, 100%, 99%);
  padding: 50px 20px 100px 20px;
  margin: 0 auto;
  max-width: 1100px;
}
.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Active-Menu {
  width: 0px;
  height: 100vh;
  position: fixed;
  right: 0px;
  top: 0px;
  background-color: white;
  padding: 50px 40px 0px 40px;
  overflow: hidden;
}
.Active-Menu img {
  margin-left: 90%;
}
.Active-Menu div {
  margin-top: 30%;
}
.Active-Menu h3 {
  font-size: 1.3rem;
  margin-top: 30px;
}
.Active-Menu h3:hover {
  color: hsl(5, 85%, 63%);
  cursor: pointer;
}
.DesktopMenu h3:hover {
  color: hsl(5, 85%, 63%);
  cursor: pointer;
}
.DesktopMenu {
  width: 70%;
  display: none;
  font-size: 0.7rem;
  justify-content: space-evenly;
  max-width: 500px;
}
main {
  margin-top: 40px;
}
.hero {
  width: 100%;
  height: auto;
  grid-area: content;
}

.hero-article {
  margin-top: 40px;
}
.hero-article h1 {
  font-size: 4rem;
}
.hero-article p {
  font-size: 1.5rem;
  color: hsl(236, 13%, 42%);
}
.hero-article button {
  margin-top: 40px;
  padding: 20px 60px;
  border: none;
  font-weight: bold;
  font-size: 1.5rem;
  background-color: hsl(5, 85%, 63%);
  color: white;
  letter-spacing: 5px;
}
button:hover {
  background-color: hsl(240, 100%, 5%);
}
.new-section {
  width: 100%;
  background-color: hsl(240, 100%, 5%);
  color: white;
  margin-top: 100px;
  padding: 20px 30px;
  line-height: 30px;
  grid-area: new;
}
.new-section h2 {
  font-size: 3rem;
  color: hsl(35, 77%, 62%);
  margin: 50px 0px;
}
.new-section article {
  margin-top: 40px;
}
.new-section p {
  font-size: 1.3rem;
  color: hsl(233, 8%, 79%);
}
.new-section h3 {
  font-size: 1.8rem;
  font-weight: 900;
  margin-bottom: 10px;
}
.new-section h3:hover {
  color: hsl(35, 77%, 62%);
}
.new-section hr {
  margin: 40px 0px;
}
footer {
  margin-top: 60px;
  grid-area: footer;
}
.text-and-img-con {
  display: flex;
  margin-top: 80px;
  justify-content: flex-start;
}
.text-and-img-con img {
  width: 30%;
  height: auto;
}
.text-con {
  margin-left: 30px;
}
.text-con h2 {
  font-size: 2.8rem;
  color: hsl(233, 8%, 79%);
}
.text-con h3 {
  font-size: 1.5rem;
  margin-top: 15px;
}
.text-con p {
  font-size: 1.3rem;
  color: hsl(236, 13%, 42%);
  margin-top: 20px;
}
.text-con h3:hover {
  color: hsl(5, 85%, 63%);
}
@media (min-width: 600px) {
  .DesktopMenu {
    display: flex;
  }

  .hamburger {
    display: none;
  }
  .contents {
    display: grid;
    gap: 30px;
    grid-template-columns: 1fr 0.5fr;
    grid-template-rows: auto auto;
    grid-template-areas:
      "content new"
      "footer footer";
  }
  .hero {
    width: 100%;
    height: 280px;
  }

  .hero-article {
    margin-top: 20px;
    display: flex;
    gap: 20px;
  }
  .hero-article h1 {
    font-size: 3rem;
  }
  .hero-article div {
    width: 90%;
  }
  .hero-article p {
    font-size: 1rem;
  }
  .new-section {
    margin-top: 40px;
  }
  .hero-article button {
    padding: 10px 30px;

    font-size: 1rem;

    letter-spacing: 1px;
  }
  .new-section h2 {
    font-size: 2rem;
    color: hsl(35, 77%, 62%);
    margin: 10px 0px;
  }
  .new-section article {
    margin-top: 20px;
  }
  .new-section p {
    font-size: 1rem;
  }
  .new-section h3 {
    font-size: 1.2rem;

    margin-bottom: 10px;
  }
  .new-section hr {
    margin: 20px 0px;
  }
  footer {
    width: 100%;
    margin-top: 1px;
    display: flex;
    justify-content: space-between;
    gap: 5px;
  }
  .text-and-img-con {
    margin-top: 0px;
  }
  .text-con {
    margin-left: 30px;
  }
  .text-con h2 {
    font-size: 2.4rem;
  }
  .text-con h3 {
    font-size: 1.2rem;
    margin-top: 5px;
  }
  .text-con p {
    font-size: 1rem;
    margin-top: 10px;
  }
}
